import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {InternBrowserProvider} from "../../providers/intern-browser/intern-browser";

/**
 * Generated class for the MessageCenterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-message-center',
    templateUrl: 'message-center.html',
    providers: [InternBrowserProvider]
})
export class MessageCenterPage {

    constructor(public viewCtrl: ViewController, public navParams: NavParams, private browser: InternBrowserProvider) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad MessageCenterPage');

        this.browser.loadUrl("https://clinetplatforms.com");
        this.browser.show();
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

}



