import {Injectable} from '@angular/core';
import {Http} from '@angular/http';
import 'rxjs/add/operator/map';
import {InAppBrowser, InAppBrowserObject, InAppBrowserOptions} from "@ionic-native/in-app-browser";

/*
  Generated class for the InternBrowserProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class InternBrowserProvider {

    private inst: InAppBrowserObject = null;
    private options: InAppBrowserOptions = {};

    private subscriptions = {};

    constructor(public http: Http, private iab: InAppBrowser) {
        console.log('Hello InternBrowserProvider Provider');
    }

    public loadUrl(url: string) {
        if (this.inst == null) {
            this.inst = this.iab.create(url, '_blank', this.options);
            return;
        }
        this.inst.executeScript({code: "window.location.href = " + url + ";"})
    }

    public getOption(name) {
        return this.options[name];
    }

    public setOption(name, value) {
        this.options[name] = value;
    }

    public css(css: string, callback = (_) => {
    }) {
        if (this.inst == null) return;

        this.inst.insertCSS({code: css}).then(result => callback(result));
    }

    public unregisterEvent(name: string) {
        if (this.inst == null || this.subscriptions[name] == null) return;

        const subscription = this.subscriptions[name];
        subscription.unsubscribe();
    }

    public registerEvent(name: string, callback) {
        if (this.inst == null) return;

        this.subscriptions[name] = this.inst.on(name).subscribe(event => callback(event));


    }

    public show() {
        if (this.inst == null) return;

        this.inst.show();
    }

    public hide() {
        if (this.inst == null) return;

        this.inst.hide();
    }
}



